<template>
  <div id="home">
    <!--  顶部导航条  -->
    <nav-bar class="home-nav">
      <div slot="center">购物街</div>
    </nav-bar>

    <tab-control :titles="['流行', '新款', '精选']"
                 @tabClick="tabClick"
                 ref="tabControl1"
                 class="tab-control"
                 v-show="isTabFixed"/>

    <scroll class="content"
            ref="scroll"
            :probe-type="3"
            @scroll="contentScroll"
            :pull-up-load="true"
            @pullingUp="loadMore">                       <!--  滚动区域  -->
      <home-swiper :banners="banners" @swiperImageLoad="swiperImageLoad"/>                 <!--  轮播图  -->
      <home-recommend-view :recommends="recommends"/>   <!--   推荐列表  -->
      <home-feature-view/>                              <!--  个性组件  -->
      <tab-control :titles="['流行', '新款', '精选']"
                   @tabClick="tabClick"
                   ref="tabControl2"/>  <!--  中部标签栏  -->
      <goods-list :goods="goods[currentType].list"/>     <!--  商品列表  -->
    </scroll>

    <back-top @click.native="backClick" v-show="isShowBackTop"/>   <!-- 回到顶部 -->
  </div>
</template>

<script>
// 首页公共子组件
import NavBar from 'components/common/navbar/NavBar.vue'  // 首页顶部导航条
import TabControl from "components/content/tabControl/TabControl" // 中部标签栏
import GoodsList from "components/content/goods/GoodsList"; // 首页商品列表
import Scroll from "components/common/scroll//Scroll"   // 滚动条组件
import BackTop from 'components/content/backtop/BackTop'  // 回到顶部组件
// 首页子组件
import HomeSwiper from './childComps/HomeSwiper'  // 导入轮播图组件
import HomeRecommendView from "./childComps/HomeRecommendView"  // 导入推荐列表组件
import HomeFeatureView from "./childComps/HomeFeatureView"  // 导入个性组件

import { getHomeMultidata, getHomeGoods } from "network/home"  // 首页网络请求
import { debounce } from 'common/utils'

export default {
  name: 'Home',
  components: {
    NavBar,
    TabControl,
    HomeSwiper,
    HomeRecommendView,
    HomeFeatureView,
    GoodsList,
    Scroll,
    BackTop
  },
  data () {
    return {
      banners: [],
      recommends: [],
      goods: {
        'pop': {page: 0, list: []},
        'new': {page: 0, list: []},
        'sell': {page: 0, list: []}
      },
      currentType: 'pop',
      isShowBackTop: false,
      tabOffsetTop: 0,
      isTabFixed: false,
      saveY: 0,
    }
  },
  // create中只写主要逻辑，具体方法封装到methods中
  created () {
    // 1.请求多个数据
    this.getHomeMultidata()
    // 2.请求商品数据
    this.getHomeGoods('pop')
    this.getHomeGoods('new')
    this.getHomeGoods('sell')

  },
  methods: {
    /*
    * 事件监听相关方法
    * */

    // 监听标签点击事件，传入外组件
    tabClick(index) {
      switch (index) {
        case 0:
          this.currentType = 'pop'
          break
        case 1:
          this.currentType = 'new'
          break
        case 2:
          this.currentType = 'sell'
          break
      }
      // 让两个tabControl的index保持一致
      this.$refs.tabControl1.currentIndex = index
      this.$refs.tabControl2.currentIndex = index
    },
    // 回到顶部事件
    backClick (){
      // 通过refs拿到上面的scroll组件对象，通过对象获取scroll属性，再调用属性方法
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    // 监听当前滚动位置
    contentScroll (position) {
      // 1.判断BackTop是否显示
      this.isShowBackTop = (-position.y) > 1000
      // 2.决定tabControl是否吸顶（position：fixed）
      this.isTabFixed = (-position.y) > this.tabOffsetTop
    },
    // 监听加载更多事件
    loadMore () {
      this.getHomeGoods(this.currentType)
      // 手动刷新，重新计算高度
      // this.$refs.scroll.scroll.refresh()
    },
    //  监听获取tabControl的offsetTop
    swiperImageLoad () {
      // 所有组件都有一个属性 $el，用于获取组件中的元素
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
    },
    /*
    * 网络请求相关方法
    * */
    getHomeMultidata () {
      getHomeMultidata().then(res => {
        // 这里的 this 指向 create(), 即当前组件对象
        this.banners = res.data.banner.list
        this.recommends = res.data.recommend.list
      })
    },
    getHomeGoods (type) {
      const page = this.goods[type].page + 1
      getHomeGoods(type, page).then(res => {
        // for (let item of res.data.list) {
        //   this.goods[type].list.push(res.data.list)
        // }
        // “...”代替遍历，效果：一个个取再push进去
        this.goods[type].list.push(...res.data.list)
        // 多请求过去后需要page+1
        this.goods[type].page += 1

        // 完成上拉加载更多
        this.$refs.scroll.finishPullUp()
      })
    }
  },
  mounted () {
    // 1.防抖,图片加载完成的事件监听
    const refresh = debounce(this.$refs.scroll.refresh, 500)
    // 监听GoodsListItem中图片加载完成
    this.$bus.$on('homeItemImgLoad', () => {
      // 监听每一张图片是否加载完成，只要有一张图片加载完成了，执行一次refresh
      // this.$refs.scroll.refresh(）
      // 封装成防抖闭包
      refresh()
      // 在前面加“this.$refs.scroll && ” 原因：
      //  先判断有没有拿到这个对象，如果没有，直接短路与
    })

  },
  // 进入组件激活
  activated () {
    this.$refs.scroll.scrollTo(0, this.saveY, 0)
    this.$refs.scroll.refresh()
  },
  // 离开组件激活
  deactivated () {
    // 保存当前滚动位置
    this.saveY = this.$refs.scroll.getCurrentY()

    // 取消全局事件监听
    // this.$bus.$off('itemImgLoad')
  }
}
</script>

