<template>
  <div class="tab-control">
    <!-- 这里不使用插槽，因为只改文字，用props传值就行 -->
    <div v-for="(item, index) in titles"
         class="tab-control-item"
         :class="{active: index === currentIndex}"
         @click="itemClick(index)">
      <span>{{ item }}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TabControl",
    props: {
      titles: {
        type: Array,
        default () {
          return []
        }
      }
    },
    data() {
      return {
        currentIndex: 0
      }
    },
    methods: {
      itemClick (index) {
        this.currentIndex = index
        // 将点击事件获取的index传到Home父组件
        this.$emit('tabClick', index)
      }
    }
  }
</script>

<style scoped>
  .tab-control {
    display: flex;
    text-align: center;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    background-color: #fff;
    margin-top: -1px;
  }
  .tab-control-item {
    flex: 1;
  }
  .tab-control-item span {
    padding: 5px;
  }

  .active {
    color: var(--color-high-text);

  }
  .active span{
    border-bottom: 3px solid var(--color-tint);
  }
</style>
