<!--  轮播图组件-->

<template>
  <swiper>
    <swiper-item v-for="(item, index) in banners" :key="index">
      <a :href="item.link">
        <img :src="item.image" alt="" @load="imageLoad">
      </a>
    </swiper-item>
  </swiper>
</template>

<script>
  import { Swiper, SwiperItem } from 'components/common/swiper'

  export default {
    name: "HomeSwiper",
    data() {
      return {
        isLoad: false
      }
    },
    props: {
      banners: {
        type: Array,
        default () {
          return []
        }
      }
    },
    components: {
      Swiper,
      SwiperItem
    },
    methods: {
      // 发出轮播图加载完毕事件
      imageLoad () {
        // 判断第一张图片是否加载完成，只发送一次emit高度（原发送4次），提高效率
        if (!this.isLoad) {
          this.$emit('swiperImageLoad')
          this.isLoad = true
        }

      }
    }
  }
</script>

